:root {
  --color-primary: #333333;
  --color-secondary: #6cb71d;
  --color-white: #ffffff;
  --color-quinary: #34495e;
  --color-modal: #33333394;
  --color-gray: #bdbec0;
  --border-radius: 0.625rem;
  --font-family-base: "Roboto", sans-serif;
  --font-size-small: 0.875rem; /* 14px */
  --font-size-base: 1rem; /* 16px */
  --font-size-large: 1.25rem; /* 20px */
  --font-size-xlarge: 1.5rem; /* 24px */
  --font-size-xxlarge: 1.875rem; /* 30px */
  --font-size-title: 4rem; /* 64px */
}

.container {
  width: 100%;
  max-width: clamp(320px, 90vw, 1200px); /* адаптивная ширина */
  margin: 0 auto;
}

@media (max-width: 768px) {
  .header__top-logo svg {
    width: 110px;
  }
  .header__top .header__top-contacts {
    justify-items: flex-end;
    text-align: right;
    justify-content: end;
  }
  .header__top-item > span {
    display: none;
  }
  .header__top-item svg {
    width: 1.875rem;
    height: 1.875rem;
    border-radius: 50%;
    padding: 5px;
    color: var(--color-white);
    background-color: var(--color-primary);
  }
  .header__top-item:hover .header__item-sub {
    opacity: 1;
  }
}
body {
  font-family: var(--font-family-base);
  background-color: var(--color-white);
  color: var(--color-primary);
  margin: 0;
}

.header {
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  background-color: var(--color-white);
}

.header__top .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-top-logo {
  width: 100%;
  color: var(--color-secondary);
}

.header__top {
  padding: 1.375rem 0 1.125rem;
  border-bottom: 1px solid var(--color-gray);
}
.header__top-contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.25rem;
  width: 60%;
}
.header__top-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.5625rem;
  padding: 10px 0;
  white-space: nowrap;
  cursor: pointer;
}

.header__item-sub {
  opacity: 0;
  transition: 0.2s;
  bottom: -25px;
  left: -80%;
  background-color: var(--color-gray);
  position: absolute;
  padding: 5px 10px;
  border-radius: 4px;
  color: #000;
}

.header__item-sub.sub--telephone {
  left: -230%;
}

.header__item-sub.sub--last {
  bottom: -50px;
}

.header__bottom {
  width: 100%;
  position: fixed;
}

.header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-white);
}

.header__menu {
  display: flex;
  align-items: center;
  gap: 2.0625rem;
  padding: 1.0625rem 0;
}
.header__menu__link {
  padding: 0.625rem 0;
  font-size: 1.125rem;
}

.header__card {
  display: flex;
  align-items: center;
  gap: 1.25rem;
  padding: 0.625rem 0;
}
.header__card-icon {
  position: relative;
}
.header__card-count {
  position: absolute;
  top: -7px;
  right: -10px;
  width: 1.125rem;
  align-self: center;
  height: 1.125rem;
  color: var(--color-white);
  font-weight: 900;
  font-size: 0.75rem;
  background-color: var(--color-secondary);
  border-radius: 50%;
}